<template>
    <div>
        <router-view/>
        <div class="bottom-section">
            <ul :class="{home:true, active:isHomeStyle}" @click="goPage('/index')">
                <li></li>
                <li>首页</li>
            </ul>
            <ul :class="{cart:true, active:iscartStyle}" @click="goPage('/cart')">
                <li></li>
                <li>购物车</li>
                <li data-v-7d39b64d="" v-show="$store.state.cart.goodsDatas.length>0" class="spot"></li>
            </ul>
            <ul :class="{my:true, active:ispcenterStyle}" @click="goPage('/pcenter')">
                <li></li>
                <li>我的</li>
            </ul>
        </div>
    </div>

</template>

<script>
    export default {
        data(){
            return{
                isHomeStyle:false,
                iscartStyle:false,
                ispcenterStyle:false,

            }
        },
        methods:{
            //路由跳转
          goPage(url){
              this.$router.replace(url);
          },
          //点击home cart my 路由方式样式的改变
            changeSytleNav(name){
              switch (name) {
                  case "index":
                      this.isHomeStyle = true;
                      this.iscartStyle = false;
                      this.ispcenterStyle = false;
                      break;
                  case "cart":
                      this.isHomeStyle = false;
                      this.iscartStyle = true;
                      this.ispcenterStyle = false;
                      break;
                  case "pcenter":
                      this.isHomeStyle = false;
                      this.iscartStyle = false;
                      this.ispcenterStyle = true;
                      break;
                  default:
                      this.isHomeStyle = true;
                      this.iscartStyle = false;
                      this.ispcenterStyle = false;
                      break;
              }
            }
        },
        created(){

            //网页title跟着路由变化
            document.title = this.$route.meta.title;
            //运行 路由样式改变方法
            this.changeSytleNav(this.$route.name);
        },
        beforeRouteUpdate(to,from,next){

            //路由更新渲染 地址不变 后缀变
            document.title = to.meta.title;
            //运行 路由样式改变方法
            this.changeSytleNav(to.name);
            next();
        },
        //keep-alive进入时触发
        activated(){
            //网页title跟着路由变化
            document.title = this.$route.meta.title;
            //运行 路由样式改变方法
            this.changeSytleNav(this.$route.name);
        }
    }
</script>

<style scoped>
    .bottom-section {
        position: fixed;
        left: 0;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        width: 100%;
        height: 1.2rem;
        padding: 0 .5rem;
        box-shadow: 0 0 10px #efefef;
        background: #fff;
    }

    .bottom-section .home, .bottom-section .cart, .bottom-section .my {
        position: relative;
        width: 1rem;
        font-size: .28rem;
        text-align: center;
    }

    .bottom-section .home li:first-child, .bottom-section .cart li:first-child, .bottom-section .my li:first-child {
        width: .6rem;
        height: .6rem;

    }
    .bottom-section ul .spot[data-v-7d39b64d] {
        width: .2rem;
        height: .2rem;
        position: absolute;
        right: 0;
        top: 0;
        z-index: 2;
        background-color: #eb1625;
        border-radius: 100%;
    }
    .bottom-section .active li:nth-child(2){
        color: #eb1625;
    }
    .bottom-section .home.active li:first-child {
        background: url("../../assets/images/home/main/home2.png") no-repeat;
        background-size: 100%;
        background-position: center;
        margin: 0 auto;
    }
    .bottom-section .cart.active li:first-child {
        background: url("../../assets/images/home/main/cart2.png") no-repeat;
        background-size: 100%;
        background-position: center;
        margin: 0 auto;
    }
    .bottom-section .my.active li:first-child {
        background: url("../../assets/images/home/main/my2.png") no-repeat;
        background-size: 100%;
        background-position: center;
        margin: 0 auto;
    }
    .bottom-section .home li:first-child {
        background: url("../../assets/images/home/main/home1.png") no-repeat;
        background-size: 100%;
        background-position: center;
        margin: 0 auto;
    }

    .bottom-section .cart li:first-child {
        background: url("../../assets/images/home/main/cart1.png") no-repeat;
        background-size: 100%;
        background-position: center;
        margin: 0 auto;
    }

    .bottom-section .my li:first-child {
        background: url("../../assets/images/home/main/my1.png") no-repeat;
        background-size: 100%;
        background-position: center;
        margin: 0 auto;
    }

</style>